@import url(https://fonts.googleapis.com/css?family=Lato|Noto+Sans+SC);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.alice-carousel__prev-btn-item,.alice-carousel__next-btn-item, .alice-carousel__dots-item:hover, .alice-carousel__dots-item.__active{
  color:#009688
}

.alice-carousel__prev-btn-item:hover,.alice-carousel__next-btn-item:hover{
  color: white;
}

.copy{
  background-color: rgba(0,0,0,.8);
}

.foot h6 {
  color: #555454;
}

ul:not(.browser-default)>li {
  list-style-type: none;
}
.container-movies .content-movies{
  background: white;
}
.container-movies .content-movies h1{
  font-size: 4.2rem;
  line-height: 110%;
  margin: 0 0 1.68rem 0;
  padding-top: 15vh;
  color: #009688;
}

.container-movies .content-movies .row .col-md-4 .card .image{
    display: block;
    border-radius: 2px 2px 0 0;
    position: relative;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
}

.container-movies .content-movies .row .col-md-4 .card{
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
  transition: box-shadow .25s;
    border-radius: 2px;
}

.container-movies .content-movies .row .col-md-4 .card .card-content{
  padding: 24px;
    border-radius: 0 0 2px 2px;
}

.container-movies .content-movies .row .col-md-4 .card .card-content .card-descrip{
  color: #424242 !important;
}

.container-movies .content-movies .row .col-md-4 .card .card-content .card-title{
  font-size: 24px;
  font-weight: 300;
  color: #009688;
}


.bg-main{
  width: 100%;
  height: 100vh;
  background: url(/Deploy-CoolPlay-React/static/media/1.bc24b9c4.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
  
  /* 
  */
}

.bg-transparent{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, rgba(0,0,0,.7), rgba(0,0,0,.1), rgba(0,0,0,.7));  
}

.container h1, a{
    color: #4db6ac;
}

.content .title-content{
  margin-top: 15vh;
}

.container.content h1{
  font-size: 4.2rem;
  line-height: 110%;
  margin: 2.8rem 0 1.68rem 0;
}

.container.content h4{
  font-size: 2.2rem;
  line-height: 110%;
}

.button-start{
  background-color: #4db6ac !important;
  font-size: 2rem;
  padding: .4em 1em;
  color: white;
  border-radius: 7px;
  box-shadow: 0 3px 10px rgba(0,0,0,.23), 0 3px 10px rgba(0,0,0,.16);
  border: 0;
}

.button-start:hover{
  transition: box-shadow .25s;
  color: black;
}
.contact{
  background-color: rgba(0,0,0,.8);
}

.contact h2{
  font-size: 3.56rem;
    line-height: 110%;
    margin: 0 0 1.424rem 0;
}


